<template>
  <div class="user-container">
    <!-- 用户基本信息面板 -->
    <div class="user-card">
      <!-- 用户头像、姓名 -->
      <van-cell>
        <!-- 使用 title 插槽来自定义标题 -->
        <template #icon>
          <img :src="$store.state.userPhoto" alt="" class="avatar" />
        </template>
        <template #title>
          <span class="username">{{ $store.state.userName }}</span>
        </template>
        <template #label>
          <van-tag color="#fff" text-color="green">申请认证</van-tag>
        </template>
      </van-cell>
      <!-- 动态、关注、粉丝 -->
      <div class="user-data">
        <div class="user-data-item">
          <span>{{ userObj.art_count }}</span>
          <span>动态</span>
        </div>
        <div class="user-data-item">
          <span>{{ userObj.follow_count }}</span>
          <span>关注</span>
        </div>
        <div class="user-data-item">
          <span>{{ userObj.fans_count }}</span>
          <span>粉丝</span>
        </div>
      </div>
    </div>

    <!-- 操作面板 -->
    <van-cell-group class="action-card">
      <van-cell icon="edit" title="编辑资料" is-link to="/user_editor" />
      <van-cell icon="chat-o" title="小江同学" is-link to="/chat" />
      <van-cell icon="warning-o" title="退出登录" is-link @click="quitFn" />
    </van-cell-group>
  </div>
</template>

<script>
import { getUserInfoAPI } from '@/api/index.js'
// 组件的 函数调用 方式，在哪个页面用，就在哪个页面引入，无需全局注册
import { Dialog } from 'vant'
import { removeToken } from '@/utils/token.js'
import { mapMutations } from 'vuex'
export default {
  name: 'User',
  data() {
    return {
      userObj: {} // 用户对象
    }
  },
  // ajax请求回来的数据一定要转存到data中！！！！
  async created() {
    const res = await getUserInfoAPI()
    console.log(res, '自己基本信息')
    this.userObj = res.data.data
    // 调用mutations里的方法，把头像存储到vuex中
    // 方式1：直接使用
    // this.$store.comit('SET_USERPHOTO', this.userObj.photo)
    // 方法2：映射使用——映射到methods方法中——借助mapMutations函数
    this.SET_USERPHOTO(this.userObj.photo)
  },
  methods: {
    // 退出登录
    quitFn() {
      Dialog.confirm({
        title: '是否退出登录',
        message: '这就走了？不爱我了吗？'
      })
        .then(() => {
          // 用户点击“确认”选项 -> 内部会调用resolve触发then
          // on confirm
          // 业务：清除token并跳转到登录页面
          removeToken()
          this.$router.replace('/login')
        })
        .catch(() => {
          // 用户点击“取消”选项 -> 内部会调用reject触发catch
          // on cancel
        })
    },
    // 方法2：映射使用——映射到methods方法——借助mapMutations函数
    ...mapMutations(['SET_USERPHOTO'])
  }
}
// 主动退出 -> 用户点击退出，清空token，强制replace切换登录页
// 被动退出 -> 把token值传后台，后台返回401 -> 响应拦截器发现401状态证明你身份过期 -> 强制进登录页
</script>

<style scoped lang="less">
.user-container {
  .user-card {
    background-color: pink;
    color: white;
    padding-top: 20px;
    .van-cell {
      background: pink;
      color: white;
      &::after {
        display: none;
      }
      .avatar {
        width: 60px;
        height: 60px;
        background-color: #fff;
        border-radius: 50%;
        margin-right: 10px;
      }
      .username {
        font-size: 14px;
        font-weight: bold;
      }
    }
  }
  .user-data {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 14px;
    padding: 30px 0;
    .user-data-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 33.33%;
    }
  }
}
</style>
